@import url('https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&display=swap');
/* ----------TESTIMONIAL---------- */
#testimonial {
    height: 100vh;
    width: 100%;
    padding: 100px 150px;
    background: var(--color-background);
  }
  
  .testimonial-title {
    /* display: flex; */
    /* flex-direction: row; */
    max-width: 50%;
    justify-content: space-between;
    align-items: end;
  }
  
  .testimonial-title h2 {
    margin: 0;
    padding: 0;
    color: var(--color-text);
  }
  
  .testimonial-title p {
    color: #7c7d81;
    font-size: 14px;
    align-self: end;
    letter-spacing: -0.5px;
    font-weight: 600;
    margin: 6px;
  }
  #testimonial ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  #testimonial li {
    list-style: none;
    padding: 0 10px;
    position: relative;
  }

  a:link,a:active,a:visited,a:hover{
    color: var(--color-accent);
  }

  .outerdiv
  {
    width: 100%;
    padding: 0;
    margin: 0;
      min-height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
    background: var(--color-background);
  }  

  .innerdiv
{
	transform: scale(0.9);
	margin: 1rem;
	display: grid;
	grid-gap: 1.5rem;
	grid-template-rows: repeat(2,22rem);
	grid-template-columns: repeat(4,17rem);
}
.eachdiv
{
	padding: 1rem 2rem;
    border-radius: 0.8rem;
    box-shadow: 5px 5px 20px #6d6b6b6b;
    color: var(--color-text);
    background: var(--color-card);
}
.div1
{
	background: #733FC8;
    grid-column: 1/3;
    grid-row: 1/2;
    background-image: url(https://raw.githubusercontent.com/RahulSahOfficial/testimonials_grid_section/5532c958b7d3c9b910a216b198fdd21c73112d84/images/bg-pattern-quotation.svg);
    background-repeat: no-repeat;
    background-position-x: 25rem;
}
.div2
{
	background:#49556B;
    grid-column: 3/4;
    grid-row: 1/2;
}
.div3
{
	background: var(--color-card);
    grid-column: 4/5;
    grid-row: 1/3;
    color: var(--color-background);
}
.div4
{
	background: var(--color-card);
	grid-column: 1/2;
    grid-row: 2/3;
    color: var(--color-background);
}
.div5
{
	background: #18202D;
	grid-column: 2/4;
    grid-row: 2/3;
}
.userdetails
{
	display: flex;
}
.imgbox
{
	margin-right: 1rem;
}
.imgbox img
{
	border-radius: 50%;
	width: 2rem;
	border: 2px solid #cec5c5;
}
.detbox
{
	display: flex;
    flex-direction: column;
    justify-content: center;
}
.detbox p
{
	margin: 0;
}
.detbox .name
{
	color: hsl(0, 0%, 81%);
    font-size: 0.9rem;
    margin-bottom: 0.1rem;
    font-weight: 600;
    font-family: 'Barlow Semi Condensed', sans-serif !important;
}
.detbox .name.dark
{
	color: #49505A;
}
.detbox .designation
{
	color: hsl(0, 0%, 81%);
    opacity: 50%;
    font-size: 0.8rem;
    font-family: 'Barlow Semi Condensed', sans-serif !important;
}
.detbox .designation.dark
{
	color: #49505A;
}
.socialLinks{
  margin-left: auto;
}
.review h4
{
	font-size: 1.4rem;
	color: #F3DEFF;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 0.8rem;
    font-family: 'Barlow Semi Condensed', sans-serif !important;
}
.review.dark h4{
	color:#4B5258;
}
.review p
{
	font-size: 0.95rem;
    color: #F3DEFF;
    font-weight: 500;
    opacity: 50%;
    line-height: 1.5;
    font-family: 'Barlow Semi Condensed', sans-serif !important;
}
.review.dark p{
	color: #0e0e0e;
}
.attribution
{
  font-family: 'Barlow Semi Condensed', sans-serif !important;
	font-size: 1rem;
    line-height: 1.5;
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    text-align: right;
}
.attribution a
{
	text-decoration: none;
}

@media only screen and (max-width: 1000px)
{
  #testimonial {
    height: 100vh;
    width: 100%;
    padding: 100px 150px;
  }
	.innerdiv
	{
		transform: scale(0.7);
	}
}
@media only screen and (max-width: 800px)
{
  #testimonial {
    height: 100vh;
    width: 100%;
    padding: 10px 15px;
  }
	.innerdiv
	{
		transform: scale(0.6);
	}
}
@media only screen and (max-width: 600px)
{
  #testimonial {
    height: 100vh;
    width: 100%;
    padding: 10px 15px;
  }
	.div1 {
		background-position-x: 10rem;
	}
	.innerdiv
	{
		display: flex; 
		flex-direction: column;
		transform: scale(1);
		margin: 2rem;
		margin-bottom: 5rem;
	}
	.attribution
	{
		position: relative;
	}
}